.upload-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.upload-box {
  background-color: #333333;
  padding: 40px;
  border-radius: 15px;
  text-align: center;
  width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  color: #ffffff;
}

.upload-box p {
  font-size: 20px;
  margin-bottom: 20px;
  color: white;
}

.upload-button {
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: #444;
  color: white;
  margin-bottom: 20px;
}

.text-input {
  width: 100%;
  height: 80px;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #444;
  border-radius: 5px;
  background-color: #1e1e1e;
  color: white;
  resize: none;
  margin-bottom: 20px;
}

.submit-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  background-color: whitesmoke;
  color: black;
  transition: background-color 0.3s ease;
  margin: 5px;
}

.submit-button:hover {
  background-color: grey;
}

.result {
  font-size: 1.2em;
  color: #eeeeee;
  margin-top: 20px;
}
