.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  background-color: #8a6d30;
  color: white;
  position: relative;
}

.logo {
  position: absolute;
  left: 1rem;
}

.logo img {
  height: 50px;
}

.menu a {
  font-size: 20px;
  color: #122620;
  text-decoration: none;
}

.menu a:hover {
  text-decoration: underline;
}